@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
*,
*::after,
*::before {
  box-sizing: border-box;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page {
  height: 100%;
}
.page__body {
  min-width: 320px;
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  margin: 0;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}
.page-header__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding-top: 11px;
  padding-left: 20px;
  padding-right: 20px;
}
@media (min-width: 768px) {
  .page-header__wrapper {
    flex-direction: row;
    justify-content: space-between;
    padding-top: 24px;
    padding-left: 30px;
    padding-right: 30px;
  }
}
@media (min-width: 1440px) {
  .page-header__wrapper {
    flex-direction: row;
    justify-content: space-between;
    padding-top: 53px;
    padding-left: 110px;
    padding-right: 110px;
  }
}
.page-header__logo-wrapper {
  display: grid;
  grid-template-columns: 1fr;
  align-items: center;
  margin-bottom: 17px;
}
@media (min-width: 768px) {
