.page-footer__text-logo {
  display: inline-block;
  line-height: 0;
}
.page-footer__text-logo[href]:hover {
  opacity: 80%;
}
.page-footer__text-logo[href]:active {
  opacity: 60%;
}
.page-footer__links {
  display: flex;
  margin: 0;
  padding: 0;
  font-style: normal;
}
@media (max-width: 767px) {
  .page-footer__links {
    flex-direction: column;
  }
}
@media (min-width: 768px) {
  .page-footer__links {
    width: 100%;
  }
}
.social-links {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (max-width: 767px) {
  .social-links {
    height: 64px;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #d9d9d9;
  }
}
@media (min-width: 768px) {
  .social-links {
    margin: 0 auto;
  }
}
.social-links__item:not(:last-child) {
  margin-right: 36px;
}
@media (min-width: 768px) {
  .social-links__item:not(:last-child) {
    margin-right: 28px;
  }
}
@media (min-width: 768px) {
  .social-links__item {
    display: flex;
    align-items: center;
  }
  .social-links__link--vk svg {
    width: 30px;
    height: 32px;
  }
  .social-links__link--instagram svg {
    width: 23px;
    height: 23px;
  }
  .social-links__link--facebook svg {
    width: 28px;
    height: 33px;
  }
}
.social-links__link svg {
  fill: #666666;
}
.social-links__link:hover svg {
  fill: #68b738;
}
.social-links__link:active svg {
  fill: #68b738;
  fill-opacity: 30%;
}
.copyright-links {
  display: flex;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (max-width: 767px) {
  .copyright-links {
    justify-content: space-between;
    height: 78px;
  }
}
.copyright-links__item {
  line-height: 0;
}
@media (min-width: 768px) {
  .copyright-links__item:first-child {
    margin-right: 22px;
  }
}
.copyright-links__link--text {
  font-family: "Arial";
  font-size: 16px;
  line-height: 18px;
  font-style: normal;
  color: #444444;
}
.copyright-links__link--text:active {
  opacity: 30%;
}
.copyright-links__link--logo {
  display: inline-block;
  line-height: 0;
}
.copyright-links__link--logo svg {
  fill: #666666;
}
.copyright-links__link--logo:hover svg {
  fill: #68b738;
}
.copyright-links__link--logo:active svg {
  fill-opacity: 30%;
}
.map__text-wrapper {
  margin: 0 auto;
  padding-top: 28px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 26px;
  background-color: #f2f2f2;
}
@media (min-width: 768px) {
  .map__text-wrapper {
    padding-top: 0;
    padding-left: 30px;
    padding-right: 30px;
    padding-bottom: 0;
  }
}
@media (min-width: 1440px) {
  .map__text-wrapper {
    position: relative;
    padding-left: 110px;
    padding-right: 110px;
  }
}
.map__text-wrapper--index-page {
  background-color: #ffffff;
}
.map__text-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}
@media (min-width: 768px) {
  .map__text-content {
    height: 201px;
    padding-right: 41px;
    padding-left: 41px;
  }
}
@media (min-width: 1440px) {
  .map__text-content {
    position: absolute;
    top: 95px;
    width: 570px;
    height: 201px;
    background-color: #ffffff;
    z-index: 1;
  }
}
.map__title {
  margin: 0;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .map__title {
    font-size: 20px;
    line-height: 21px;
  }
}
.map__address {
  font-family: "Arial";
  font-size: 14px;
  line-height: 20px;
  font-style: normal;
  color: #444444;
}
.map__address .map__additional-br {
  display: none;
}
@media (min-width: 768px) and (max-width: 1439px) {
  .map__address {
    text-align: right;
  }
}
@media (min-width: 768px) {
  .map__address {
    font-size: 16px;
  }
  .map__address .map__additional-br {
    display: block;
  }
}
.map__interactive-map {
  width: 100%;
  border: none;
}
@media (min-width: 768px) {
  .map__interactive-map {
    height: 400px;
  }
}
